import React from "react";
import { Form, Button, Col } from "react-bootstrap";
import { Redirect } from "react-router";
import client from "./CLASSES/client";
import "./Login.css";
import { Username, Password, FeildsData } from "./Feilds";
import { SnackBar, sDialog } from "./Interfaces/SnackBar";
import Pure from "./Pure";
import { Loading } from "./SubComponents/ProgressBar";
import {OpenGuid} from "./Guid";

class Login extends Pure {
    state = {
        redirect: false
    }

    data = {
        username: "",
        password: "",
        server: 1,
    }

    login(event) {
        event.preventDefault();
        let a = FeildsData.username.isValid();
        let b = FeildsData.password.isValid();
        if (a && b) {
            Loading.show();
            this.data.username = FeildsData.username.value;
            this.data.password = FeildsData.password.value;
            client.login(this.data).then(response => {
                if (!this.componentIsReady) return;
                if (response.authorized === true) {
                    client.updateAuthorization(response.server, response.authorized, response.key);
                    this.setState({ redirect: true });
                } else {
                    sDialog.open(response.e_username + response.e_password);
                }
                Loading.hide();
            })
        }
        return false;
    }

    render() {
        if (this.state.redirect)
            return <Redirect to="/Village" />;
        else
            return <div className="game-login">
                <SnackBar />
                <h1 onClick={OpenGuid()}>الدخول إلى اللعبة</h1>
                <div className="game-login-top">
                    <div><span>الدخول</span></div>
                    <p>عليك تفعيل ملفات التعريف (كوكيز) لتتمكن من تسجيل الدخول. و إذا تعدد مستخدموا هذا الحاسب يجب أن تضغط على تسجيل الخروج في كل مرة تنتهي من اللعب وذلك حفاظاً على بياناتك.</p>
                </div>
                <form onSubmit={event => this.login(event)}>
                    <Form.Group className="game-login-form">
                        <Form.Row>
                            <Form.Label column sm={2}>اسم المستخدم</Form.Label>
                            <Col>
                                <Username />
                            </Col>
                        </Form.Row>
                        <Form.Row>
                            <Form.Label column sm={2}>كلمة المرور</Form.Label>
                            <Col>
                                <Password />
                            </Col>
                        </Form.Row>
                        <Form.Row>
                            <Form.Label column sm={2}>السيرفر<br /></Form.Label>
                            <Col className="servers-col">
                                <ServersList onChange={server => { this.data.server = server; }} />
                            </Col>
                        </Form.Row>
                    </Form.Group>
                    <Button variant="secondary" type="submit">تسجيل الدخول</Button>
                </form>
            </div>;
    }
}

class ServersList extends Pure {
    state = { ready: false }
    /** @type {{ serverNumber: number, ready: boolean, ended: boolean, isFull: boolean, started: number, players: number }[]} */
    servers = <></>;

    onReady() {
        client.listServers(response => {
            if (!Array.isArray(response)) return;
            this.servers = <select className="servers-list" onChange={e => this.props.onChange(Number(e.target.value))}>
                {response.map(server => <option key={"Server" + server.serverNumber} value={server.serverNumber}>السيرفر {server.serverNumber}</option>)}
            </select>;
            this.updateState({ ready: true });
        });
    }

    render() {
        return this.servers;
    }
}

export default Login;